import { Component, Directive, Inject, Input } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { LogoutService, LOGOUT } from '@backbase/foundation-ang/web-sdk';
import { Observable } from 'rxjs';
import {
  UserContextMenuWidgetPropertiesService,
  WidgetProperties,
} from '../user-context-menu-widget-properties.service';

import { UserContextMenuService, ServiceAgreement, UserInfo } from '../user-context-menu.service';

export interface UserContextMenuContainerCustomizableContext {
  currentContext: ServiceAgreement | undefined | null;
  showSingleContext: boolean | undefined | null;
  canSelectContext: boolean | null;
  dropdownMenuPosition: string | undefined | null;
  userInfo: UserInfo | undefined | null;
  widgetProperties: WidgetProperties | null;
}

@Component({
  selector: 'bb-user-context-menu-container',
  templateUrl: './user-context-menu-container.component.html',
})
export class UserContextMenuContainerComponent {
  hostRef = this;

  /**
   * Dropdown menu position
   */
  @Input() dropdownMenuPosition?: string;

  constructor(
    private readonly service: UserContextMenuService,
    private readonly widgetPropertiesService: UserContextMenuWidgetPropertiesService,
    @Inject(LOGOUT) private readonly logoutService: LogoutService,
  ) {}

  currentContext: Observable<ServiceAgreement | undefined> = this.service.currentContext;
  showSingleContext: Observable<boolean> = this.widgetPropertiesService.showSingleContext$;
  widgetProperties$: Observable<WidgetProperties> = this.widgetPropertiesService.properties$;
  canSelectContext: Observable<boolean> = this.service.canSelectContext;
  userInfo: Observable<UserInfo | undefined> = this.service.userInfo;

  showSwitchContextModal = false;
  renderContextSelect = false;
  showCancelButton = false;

  openSwitchContextModal() {
    this.showSwitchContextModal = true;
    this.renderContextSelect = true;
  }

  logout() {
    this.logoutService
      .logout()
      .catch(() => {})
      .then(() => {
        this.logoutService.goToLoginPage();
      });
  }

  /**
   * Set flag for show cancel button when loading finished
   */
  loadingChanged(event: boolean) {
    this.showCancelButton = !event;
  }
}

@Directive({
  selector: '[bbUserContextMenuContainerCustomizable]',
})
export class UserContextMenuContainerCustomizableDirective extends BbTemplate<
  UserContextMenuContainerComponent,
  UserContextMenuContainerCustomizableContext
> {}
