import { Injectable } from '@angular/core';
import { HttpResponse } from '@angular/common/http';
import {
  UserContextHttpService,
  ServiceAgreementHttpService,
  Serviceagreementpartialitem as ServiceAgreement,
} from '@backbase/data-ang/accesscontrol';
import { UserManagementService, GetOwnUser as UserInfo } from '@backbase/data-ang/user';
import { Observable, of } from 'rxjs';
import { catchError, map } from 'rxjs/operators';

export { Serviceagreementpartialitem as ServiceAgreement } from '@backbase/data-ang/accesscontrol';
export { GetOwnUser as UserInfo } from '@backbase/data-ang/user';

@Injectable()
export class UserContextMenuService {
  constructor(
    private readonly userContextService: UserContextHttpService,
    private readonly serviceAgreementService: ServiceAgreementHttpService,
    private readonly userService: UserManagementService,
  ) {}

  readonly currentContext: Observable<ServiceAgreement | undefined> = this.serviceAgreementService
    .getServiceAgreementContext()
    .pipe(catchError(() => of(undefined)));

  readonly canSelectContext: Observable<boolean> = this.getTotalServiceAgreements();

  readonly userInfo: Observable<UserInfo | undefined> = this.userService
    .getOwnUser()
    .pipe(catchError(() => of(undefined)));

  private getTotalServiceAgreements(): Observable<boolean> {
    return this.userContextService.getUserContextServiceAgreements({ from: 0, size: 2 }, 'response').pipe(
      map((response: HttpResponse<ServiceAgreement[]>) => {
        const items: ServiceAgreement[] = response.body || [];
        return (parseInt(response.headers.get('x-total-count') || '0', 10) || items.length) > 1;
      }),
      catchError(() => {
        return of(false);
      }),
    );
  }
}
