import { Injectable, Optional } from '@angular/core';
import { ItemModel } from '@backbase/foundation-ang/core';
import { Property } from '@backbase/foundation-ang/web-sdk';

import { BehaviorSubject, combineLatest, Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';

export const WidgetProperty = {
  PAGE_SIZE: 'pageSize',
  INCLUDE_CURRENT_CONTEXT: 'includeCurrentContext',
  SHOW_SEARCH_AFTER: 'showSearchAfter',
  SHOW_SINGLE_CONTEXT: 'showSingleContext',
  REDIRECT_PAGE: 'selectContextRedirectPage',
};

export interface WidgetProperties {
  pageSize: number;
  includeCurrentContext: boolean;
  showSearchAfter: number;
  selectContextRedirectPage: string;
  showSingleContext: boolean;
}

export const DefaultWidgetProperties: WidgetProperties = {
  pageSize: 7,
  includeCurrentContext: true,
  showSearchAfter: 7,
  selectContextRedirectPage: '',
  showSingleContext: true,
};

@Injectable()
export class UserContextMenuWidgetPropertiesService {
  constructor(@Optional() private readonly itemModel: ItemModel) {}

  private readonly pageSizeSubject$ = new BehaviorSubject<number | undefined>(undefined);
  private readonly includeCurrentContextSubject$ = new BehaviorSubject<boolean | undefined>(undefined);
  private readonly showSearchAfterSubject$ = new BehaviorSubject<number | undefined>(undefined);
  private readonly selectContextRedirectPageSubject$ = new BehaviorSubject<string | undefined>(undefined);
  private readonly showSingleContextSubject$ = new BehaviorSubject<boolean | undefined>(undefined);

  private readonly pageSize: Observable<number> = this.itemModel
    ? this.itemModel
        .property(WidgetProperty.PAGE_SIZE, '7')
        .pipe(map(propertyValue => parseInt(<string>propertyValue, 10)))
    : of(DefaultWidgetProperties.pageSize);

  private readonly includeCurrentContext: Observable<boolean> = this.itemModel
    ? this.itemModel
        .property(WidgetProperty.INCLUDE_CURRENT_CONTEXT, 'true')
        .pipe(map(propertyValue => `${propertyValue}` === 'true'))
    : of(DefaultWidgetProperties.includeCurrentContext);

  private readonly showSearchAfter: Observable<number> = this.itemModel
    ? this.itemModel
        .property(WidgetProperty.SHOW_SEARCH_AFTER, '7')
        .pipe(map(propertyValue => parseInt(<string>propertyValue, 10)))
    : of(DefaultWidgetProperties.showSearchAfter);

  private readonly selectContextRedirectPage: Observable<string> = this.itemModel
    ? this.itemModel.property(WidgetProperty.REDIRECT_PAGE, '')
    : of(DefaultWidgetProperties.selectContextRedirectPage);

  private readonly showSingleContext: Observable<boolean> = this.itemModel
    ? this.itemModel
        .property(WidgetProperty.SHOW_SINGLE_CONTEXT, 'true')
        .pipe(map(propertyValue => `${propertyValue}` === 'true'))
    : of(DefaultWidgetProperties.showSingleContext);

  readonly pageSize$ = this.getProperty<number>(this.pageSizeSubject$, this.pageSize).pipe(map(Number));
  readonly includeCurrentContext$ = this.getProperty<boolean>(
    this.includeCurrentContextSubject$,
    this.includeCurrentContext,
  ).pipe(map(this.toBoolean));
  readonly showSearchAfter$ = this.getProperty<number>(this.showSearchAfterSubject$, this.showSearchAfter).pipe(
    map(Number),
  );
  readonly selectContextRedirectPage$ = this.getProperty<string>(
    this.selectContextRedirectPageSubject$,
    this.selectContextRedirectPage,
  );
  readonly showSingleContext$ = this.getProperty<boolean>(this.showSingleContextSubject$, this.showSingleContext);

  private getProperty<T>(input$: Observable<T | undefined>, itemModel: Observable<Property>): Observable<T> {
    return combineLatest([input$, itemModel]).pipe(
      map(([value, valueIM]) => (value === undefined ? valueIM : value) as T),
    );
  }

  private toBoolean(value: string | boolean) {
    return value === 'true' || value === true;
  }

  setPageSize(value: number) {
    this.pageSizeSubject$.next(value);
  }

  setIncludeCurrentContext(value: boolean) {
    this.includeCurrentContextSubject$.next(value);
  }

  setShowSearchAfter(value: number) {
    this.showSearchAfterSubject$.next(value);
  }

  setSelectContextRedirectPage(value: string) {
    this.selectContextRedirectPageSubject$.next(value);
  }

  setShowSingleContext(value: boolean) {
    this.showSingleContextSubject$.next(value);
  }

  readonly properties$: Observable<WidgetProperties> = combineLatest([
    this.pageSize$,
    this.includeCurrentContext$,
    this.showSearchAfter$,
    this.selectContextRedirectPage$,
    this.showSingleContext$,
  ]).pipe(
    map(([pageSize, includeCurrentContext, showSearchAfter, selectContextRedirectPage, showSingleContext]) => ({
      pageSize,
      includeCurrentContext,
      showSearchAfter,
      selectContextRedirectPage,
      showSingleContext,
    })),
  );
}
